.App {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Poppins', sans-serif;
}

.savings {
  background: linear-gradient(80deg,#0030cc 0%,#00a4db 100%);
  height: 90%;
  border-radius: 20px;
  padding: 2rem;
  text-align: center;
  color: white;
}

.savings h3 {
  margin-top: 0;
}

.calculator {
  padding: 2rem 2rem 2rem 0;
  margin-left: 10px;
}

.savings dl {
  font-size: 1.25em;
  display: grid;
  grid-template-columns: 45% auto;
}

.savings dt {
  grid-column-start: 1;
  text-align: right;
}

.savings dd {
  grid-column-start: 2;
  text-align: right;
  margin-inline-start: 12px;
}

.plan {
  position: relative;
  width: 100%;
  background-color: white;
  color: #8b8e93;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0,0,0,.1);
  margin-top: 3rem;
}

.plan h3 {
  font-size: 2rem;
  padding: 1.5rem 0 0 0;
  margin-bottom: 0%;
}

