.btn-outline-primary {
  color: #00e0d8;
  border: 2px solid #00e0d8; }
  .btn-outline-primary:hover {
    color: #fff !important;
    background: none;
    border: 2px solid #00e0d8; }
    .btn-outline-primary:hover i {
      color: #fff !important; }
    .btn-outline-primary:hover:after {
      left: 0; }
  .btn-outline-primary.active {
    background: none; }
  .btn-outline-primary:after {
    content: '';
    position: absolute;
    top: 0;
    left: -102%;
    width: 102%;
    height: 100%;
    background: linear-gradient(45deg, #00a8f4 0%, #02d1a1 100%);
    z-index: -1;
    transition: ease 0.3s; }

/* common */
.ribbon {
  width: 150px;
  height: 150px;
  overflow: hidden;
  position: absolute;
}
.ribbon::before,
.ribbon::after {
  position: absolute;
  z-index: -1;
  content: '';
  display: block;
  border: 5px solid #2980b9;
}
.ribbon span {
  position: absolute;
  display: block;
  width: 275px;
  padding: 15px 0;
  background-color: #3498db;
  box-shadow: 0 5px 10px rgba(0,0,0,.1);
  color: #fff;
  font: 700 18px/1 'Poppins', sans-serif;
  text-shadow: 0 1px 1px rgba(0,0,0,.2);
  text-transform: uppercase;
  text-align: center;
}

/* top left*/
.ribbon-top-left {
  top: -7px;
  left: -7px;
}
.ribbon-top-left::before,
.ribbon-top-left::after {
  border-top-color: transparent;
  border-left-color: transparent;
}
.ribbon-top-left::before {
  top: 0;
  right: 0;
}
.ribbon-top-left::after {
  bottom: 0;
  left: 0;
}
.ribbon-top-left span {
  right: -38px;
  top: 27px;
  transform: rotate(-45deg);
}